.circle:hover{
  background-color: orange !important;
  border : 5px solid rgb(255, 255, 255) !important;
  box-shadow: rgba(0, 0, 0, 0.55) 0px 25px 50px -12px;
  transition: all 0.2s ease-in-out;
}
.text-hover:hover{
  color: orange !important;
}
